/* make variables */
:root {
    --lightgray: #D9D9D9;
    --gray: #9c9c9c;
    --orange: #f48915;
    --darkGrey: #464D53;
    --caloryCard: #656565;
    --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
    --appColor: #3c3f45;
}


/* mix-blend-mode: overlay; - This sets the blending mode for the element's contents. In this case, it's set to "overlay", which creates a blending effect between the element's contents and its background.
gap: 6rem; - This sets the spacing between the child elements of the flex layout to be 6rem.
overflow: hidden; - This hides any content that overflows the boundaries of the element. */

/* على ما يبدو هذه هي صورة PNG تم تشفيرها بنظام Base64 ويتم استخدامها كصورة خلفية في ملف CSS لصفحة ويب. الترميز بنظام Base64 هو طريقة لتمثيل البيانات الثنائية مثل الصور على شكل نص ASCII حتى يمكن تضمينها بسهولة في صفحة ويب. يشير بادئة data:image/png;base64 إلى أن البيانات هي صورة PNG تم تشفيرها في تنسيق Base64. */

.App {
    background-image: url(data:image/png;base64,);
    mix-blend-mode: overlay;
    background-color: var(--appColor);
    display: flex;
    flex-direction: column;
    gap: 6rem;
    overflow: hidden;
}

/* This is a CSS pseudo-element selector that targets the scrollbar of a web page in WebKit-based browsers such as Google Chrome and Safari.

The ::-webkit-scrollbar selector allows you to modify the appearance of the scrollbar, including its width, height, background color, and thumb (the part that you drag to scroll).

In this particular case, the rule display: none is used to hide the scrollbar completely. This means that the scrollbar will not be visible to the user, even when scrolling. This could be useful in certain situations, such as when you want to create a minimalistic design without any visual distractions or when you have implemented custom scroll functionality with JavaScript or other tools.

It's important to note that this rule only affects WebKit-based browsers and will not affect the scrollbar in other browsers such as Firefox or Edge. If you want to hide the scrollbar across all browsers, you can use other CSS properties and values or JavaScript. */
::-webkit-scrollbar {
    display: none;
}

.strokeText{
    color: transparent;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
}


.btn{
    background-color: white;
    padding: 0.5rem;
    font-weight: bold;
    border: 4px solid transparent;
    transition: 300ms all;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}


.blur{
    background-color: rgba(253, 120, 43 , 1);
    position: absolute;
    border-radius: 50%;
    filter: blur(150px);
    z-index: -9;
}
